@import url(https://fonts.googleapis.com/css2?family=Merienda:wght@700&display=swap);
* {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font-family: 'Merienda', cursive;
    box-sizing: border-box;
    cursor: default;

}
button:focus {
    outline: none;
}
input:focus {
    outline: none;
}
textarea {
    resize: none;
}
.loginBackground {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;
}
.loginContainer {
    position: relative;
    padding: 50px;
    margin: 0 auto;
    top:30%;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 360px;
    min-width: 185px;
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 10px;
    color: silver;
}
.loginContainer__form {
    display: flex;
    flex-direction: column;
    
}
.loginContainer__header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    min-height: 45px;
    margin: 1rem;
}
.loginContainer__formLable {
    margin-left: 1rem;
}
.loginContainer__formInput{
    margin: .50rem 1rem .75rem;
    height: 2.5rem;
    box-sizing: border-box;
    padding: 5px;
    border-radius: 5px;
    background-color: red;
    border: none;
    cursor: text;
}
.loginContainer__signIn {
    margin: 1rem;
    height: 2.5rem;
    background-color: red;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
#loginLable {
    margin-left: 0;
}
.loginContainer__signIn:hover {
    background-color: purple;
}
#registerButton {
    border-radius: 25px;
    border: 2px solid red;
    color: silver;
    padding: 10px 14px;
    background: none;
    cursor: pointer;
}
#registerButton:hover {
    border: 2px solid purple;
    background-color: purple;
    color: black;
}
#boxes {    
    width: 100%;
    overflow: hidden;
    height: 100vh;
    position: absolute;
    z-index: -10;
}
#boxes > span {
    
    position: absolute;
    z-index: -10;
    pointer-events: none;
    background-color: blue;
    -webkit-animation: animate 5s linear infinite;
            animation: animate 5s linear infinite;
}
@-webkit-keyframes animate {
    0% {
        transform: scale(0) rotate(0deg);
        -webkit-filter: hue-rotate(0deg);
                filter: hue-rotate(0deg)
    }
    25% {
        
    }
    50% {
        
    }
    75% {
    
    }
    100% {
        transform: scale(1)  rotate(360deg);
        -webkit-filter: hue-rotate(175deg);
                filter: hue-rotate(175deg)  
    }
}
@keyframes animate {
    0% {
        transform: scale(0) rotate(0deg);
        -webkit-filter: hue-rotate(0deg);
                filter: hue-rotate(0deg)
    }
    25% {
        
    }
    50% {
        
    }
    75% {
    
    }
    100% {
        transform: scale(1)  rotate(360deg);
        -webkit-filter: hue-rotate(175deg);
                filter: hue-rotate(175deg)  
    }
}

@media screen and (min-width: 700px) {
    .loginContainer__formLable {
        font-size: 2rem;
    }
    .loginContainer {
        max-width: 600px;
    }
    .loginContainer__formInput {
        font-size: 1.25rem;
    }
    .loginContainer__signIn, #registerButton {
        font-size: 1.5rem;
    }
}
.signupBackground {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;
}
.signupContainer{
    position: relative;
    top: 20%;
}
.signupContainer__buttonsBox {
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    padding: 10px 5px;
    justify-content: space-between;
    width: 100%;
    max-width: 360px;
    min-width: 185px;
    background-color: rgba(0, 0, 0, 0.6)
}
.signupContainer__button {
    cursor: pointer;
    padding: 8px;
    border-radius: 5px;
    color: silver;
    background-color: midnightblue;
}
.signupContainer__form {
    margin: 0 auto;
    padding: 5px;
    display: flex;
    flex-direction: column;
    max-width: 360px;
    min-width: 185px;
    background-color: rgba(0, 0, 0, 0.6)
    
}
.signupContainer__formInput {
    width: 100%;
    padding: 5px 0;
    color: silver;
    text-align: center;
    background-color: midnightblue;
    cursor: text;
}
.signupContainer__text {
    text-align: center;
    padding: 5px;
    color: silver;
}
button:disabled {
    background-color: rgba(25, 25, 112, 0.7);
    cursor: not-allowed;
}
section {    
    width: 100%;
    overflow: hidden;
    height: 100vh;
    position: absolute;
    z-index: -10;
}
section > span {
    
    position: absolute;
    z-index: -10;
    pointer-events: none;
    background-color: blue;
    -webkit-animation: animate2 5s linear infinite;
            animation: animate2 5s linear infinite;
}

@-webkit-keyframes animate2 {
    0% {
        transform: scale(0);
        background-color: red;
    }
    25% {
        background-color: blue;
    }
    50% {
        background-color: blue;
    }
    75% {
        background-color: red;
    }
    100% {
        transform: scale(1);
        background-color: blue; 
    }
}

@keyframes animate2 {
    0% {
        transform: scale(0);
        background-color: red;
    }
    25% {
        background-color: blue;
    }
    50% {
        background-color: blue;
    }
    75% {
        background-color: red;
    }
    100% {
        transform: scale(1);
        background-color: blue; 
    }
}

@media screen and (min-width: 700px) {
    .signupContainer__text {
        font-size: 2rem;
    }
    .signupContainer__form {
        max-width: 600px;
    }
    .signupContainer__buttonsBox {
        max-width: 600px;
    }
    .signupContainer__formInput {
        font-size: 1.25rem;
    }
    .signupContainer__button {
        font-size: 1.5rem;
    }
}
#splashImg {
    width: 100%;
    max-width: 2000px;
    height: 100vh;
    display: block;
    margin: 0 auto;
}
.splashContainer {
  position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.backgroundColor {
    position: fixed;
    z-index: -100;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #1f262c;
}
.splashContainer__login {
    width: 100%;
    max-width: 2000px;
    display: flex;
    justify-content: flex-end;
    margin: 25px auto;
}
.splashContainer__button {
    margin-right: 10px;
    padding: 8px;
    border-radius: 10px;
    color: whitesmoke;
    background-color: transparent;
    cursor: pointer;
    font-size: 25px;
}
.splashContainer__button:hover {
    color: red;
}
#gotGame {
    font-size: 3rem;
}
#signupButton {
    background-color: transparent;
    margin: 25px 0 10px ;
    color: whitesmoke;
    font-size: 2rem;
    cursor: pointer;
}
#signupButton:hover {
    color: red;
}
.splashContainer__body {
    background-color: rgba(0, 0, 0, 0.5);
    color: whitesmoke;
    text-align: center;
    position: relative;
    top: 30%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    border-radius: 5px 120px;
    box-shadow: -10px 10px 28px 15px rgba(255, 0, 0, 0.7),
                -8px 8px 25px 12px rgb(0, 0, 0);
}


@media screen and (max-width: 550px) {
    #gotGame {
        font-size: 2rem;
    }
    #signupButton {
        font-size: 1rem;
    }
    .splashContainer__body {
        width: 325px;
        padding: 5px;
        top: 35%
    }
    .splashContainer__button {
        font-size: 1rem;
    }
}
.splashContainer__footer {
    width: 100%;
    position: fixed;
    bottom: 0;
    text-align: center;
    color: snow;
    font-size: 1.25rem;
}
.splashContainer__footer > p {
    border-bottom: 1px solid red;
    padding-bottom: 15px;
    margin-bottom: 10px;
}
.splashContainer__FooterLinks {
    margin-bottom: 20px;
}
.fab, .fas {
    cursor: pointer;
    margin: 10px 25px;
    color: silver;
}

.fab:hover, .fas:hover {
    color: red;
}
.homeBackground {
    background-color: black;
}
.homeContainer {
    width: 100%;
    max-width: 1920px;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
}
.homeContainer__left {
    position: relative;
    width: 25vw;
    min-width: 275px;
    display: flex;
    flex-direction: column;
    color: silver;
    overflow: hidden;
}
.homeContainer__leftHeader {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 85px;
}
#x {
    display: none;
    cursor: pointer;
    position: fixed;
    z-index: 3;
    top: 5px;
    right: 5px;
    padding: 5px;
    border: 1px solid silver;
}
#x:hover {
    color: red;
    border-color: red;
}
.homeContainer__profile {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 5px 10px 10px;
    font-size: 1.25rem;
    border-bottom: 2px solid red;
}
.homeContainer__labels {
    margin: 25px 15px;
    border-radius: 5px;
    text-align: center;
    font-size: 1.25rem;
    padding: 5px 0;
    box-shadow: inset 0 0 28px 8px rgba(255, 0, 0, 0.6);
}
.homeContainer__lables > h2 {
    font-size: 1.5rem;
}

.homeContainer__right {
    width: 75vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 5% 95%;
    overflow: hidden;
}
#username {
    align-self: center;
    font-size: 2rem;
    letter-spacing: 0.05rem;
}
#logout {
    padding: 5px;
    border: none;
    border-radius: 5px;
    background-color: black;
    color: red;
    font-size: 1.5rem;
    cursor: pointer;
    grid-column-start: 3;
    margin: 5px 5px auto auto;
    min-width: 115px;
}
#modalButton {
    padding: 5px;
    border: none;
    border-radius: 5px;
    background-color: black;
    color: red;
    font-size: 1.5rem;
    cursor: pointer;
    display: none;
}
#userDes {
    overflow: hidden;
    max-height: 220px;
    padding: 10px;
    overflow-y: auto;
}
.chat {
    width: 100%;
    height: 100%;
    min-height: 120px;
    overflow-y: auto;
}
#chatMessage {
    margin-top: 10px;
    padding: 10px;
}
#chatLable {
    text-align: center;
    font-size: 1.25rem;
    border-bottom: 2px solid red;
}
#sendMesageButton {
    background-color: transparent;
    margin-top: 5px;
    cursor: pointer;
    color: red;
}
.chatInput {
    width: 100%;
    display: flex;
    flex-direction: column;
}
.chatInput > input {
    width: 100%;
    background-color: silver;
    color: black;
    padding: 5px;
    cursor: text;
}
.homeContainer__feed {
    grid-column-start: 2;
    text-align: center;
    margin: auto auto;
    box-shadow: inset 0 0 28px 15px rgba(0, 0, 255, 0.6);
    border-radius: 15px;
    color: silver;
    padding: 25px;
}
.homeContainer__feedButtons {
   display: flex;
   justify-content: space-around;
}
#maybeLater, #letsPlay {
    color: red;
    background-color:  rgba(0, 0, 255, .3);
    padding: 5px;
    border-radius: 10px;
    cursor: pointer;
    font-size: 1.5rem;
    margin: 20px;
}
#feedDes {
    margin: 15px auto;
    max-width: 275px;
    max-height: 500px;
    letter-spacing: 1.25px;
    padding: 5px;
    overflow: hidden;
    overflow-y: auto;
}
#nowPlaying, #platform {
    margin-bottom:15px;
}
#feedUsername {
    margin-bottom: 15px;
    border-bottom: 1px solid blue;
    font-size: 2rem;
}
.homeContainer__feed > h2 {
    font-size: 1.5rem;
}
.homeContainer__inputLabel {
    text-align: center;
    margin-top: 15px;
}
.homeContainer__profileInputfeild {
    cursor: text;
    margin:5px 5px 0;
    width: 97%;
    background-color: silver;
    padding: 5px;
    text-align: center;
}
.homeContainer__edit {
    cursor: pointer;
}
.homeContainer__edit:hover {
    color: red;
    border-color: red;
}
#emptyFeed {
    font-size: 2rem;
}
::-webkit-scrollbar {
    width: 12px;
}
 
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgb(173, 173, 173); 
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgb(117, 117, 117);; 
}

@media screen and (max-width: 700px) {
    .homeContainer__left {
        display: none;
        height: 100vh;
        width: 100vw;
    }
    #x {
        display: block;
    }
    .homeContainer__right {
        width: 100vw;
    }
    #modalButton {
        display: block;
    }
}
.links {
    grid-column-start: 2;
    grid-row-start: 1;
    text-align: center;
}

