@import url('https://fonts.googleapis.com/css2?family=Merienda:wght@700&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font-family: 'Merienda', cursive;
    box-sizing: border-box;
    cursor: default;

}
button:focus {
    outline: none;
}
input:focus {
    outline: none;
}
textarea {
    resize: none;
}